.header-container {
  display: flex;
  flex-direction: row;
}

.sort-icons {
  margin-left: 0.75rem;
  align-content: center;
  justify-content: center;
  display: flex;
  flex-direction: column;
}

.icon {
  margin-bottom: 0.025rem;
  padding: 0.1rem;
}

.icon:hover {
  cursor: pointer;
}

.sort-arrow:hover {
  cursor: pointer;
  transform: scale(1.1);
}

.sort-arrow {
  background-color: transparent;
}

.arrow-up,
.arrow-down {
  width: 0;
  height: 0;
  border-left: 0.25rem solid transparent;
  border-right: 0.25rem solid transparent;
}

.arrow-up {
  border-bottom: 0.5rem solid #2c3e50;
  margin-bottom: 0.25rem;
}

.arrow-down {
  border-top: 0.5rem solid #2c3e50;
}

.arrow-up.active,
.arrow-down.active {
  background-color: transparent;
}

.arrow-up.active {
  border-bottom: 0.5rem solid #e74c3c;
}

.arrow-down.active {
  border-top: 0.5rem solid #e74c3c;
}
