.piechart-container {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 75%;
  align-content: center;
}

.chart-header {
  width: 100%;
}

.chart-header h4 {
  text-align: left;
  margin-top: 1rem;
}

.chart-container {
  display: flex;
  align-content: left;
  justify-content: left;
}

.piechart-container .chart-container {
  height: 25vh;
  width: 15vw;
}
